import React, {Component} from 'react'
import {queryStringToObj, albumsReducer, answerStatsReducer} from '../utils'
import parsePlaylistUri from '../core/spotify.uri.parser'

import Welcome from './Welcome'
import Game from './Game'

class GameContainer extends Component {
  constructor(props) {
    super(props)

    this.state = {
      spotify: {
        clientId: 'f2d6a59366cb405cad0b908387ee2d61',
        token: queryStringToObj(window.location.hash).access_token,
      },

      game: {
        playlistUri: '',
        startedAt: null,
        albums: [],
        currentAlbumIndex: 0,
        answers: [],
      }
    }
  }

  render() {
    const {spotify, game} = this.state
    const stats = game.answers.reduce(answerStatsReducer, {answered: 0, correct: 0, incorrect: 0, givenUp: 0})

    if (!spotify.token) {
      return (
        <Welcome
          {...game}
          onStart={() => this.authenticateOnSpotify()}
          onPlaylistUriChange={(playlistUri) => this.playlistUriChange(playlistUri)}
        />
      )
    }

    if (!game.startedAt) this.startGame()

    return (
      <Game
        {...game}
        {...stats}
        onSkip={() => this.skip()}
        onAnswerChange={(answer) => this.answerChange(answer)}
        onAnswerSubmit={() => this.answerSubmit()}
        onAnswerGiveUp={() => this.answerGiveUp()}
        onGoBack={() => this.goBack()}
      />
    )
  }

  authenticateOnSpotify() {
    const {spotify} = this.state
    const returnUrl = window.location.href.substring(0, window.location.href.length - 1)
    window.location.href =
      'https://accounts.spotify.com/authorize'
      + `?client_id=${spotify.clientId}`
      + `&response_type=token`
      + `&redirect_uri=${encodeURIComponent(returnUrl)}`
  }

  startGame() {
    this.loadPlaylist().then((playlist) => {
      const {game} = this.state
      game.startedAt = new Date()
      game.albums =
        playlist.tracks.items
          .reduce(albumsReducer, {albums: [], albumMap: {}})
          .albums

      this.setState({game})
    })
  }

  loadPlaylist() {
    const playlistUri = window.sessionStorage.getItem('playlistUri')
    const playlistInfo = parsePlaylistUri(playlistUri)
    const {user = 'lasse_nordgren', playlist = '0WMjzGw4twheZuLdsJJd5L'} = playlistInfo

    const request = {
      method: 'GET',
      headers: new Headers({'Authorization': 'Bearer ' + this.state.spotify.token})
    }

    const playlistPromise =
      fetch(`https://api.spotify.com/v1/users/${user}/playlists/${playlist}`, request)
        .then((response) => response.json())

    return playlistPromise
  }

  skip() {
    const {game} = this.state
    game.currentAlbumIndex = game.currentAlbumIndex < game.albums.length - 1 ? ++game.currentAlbumIndex : 0
    this.setState({game})
  }

  goBack() {
    const {game} = this.state
    game.currentAlbumIndex = game.currentAlbumIndex > 0 ? --game.currentAlbumIndex : game.albums.length - 1
    this.setState({game})
  }

  answerChange(answer) {
    answer.trim()
    const {game} = this.state
    game.answers[game.currentAlbumIndex] = {text: answer}
    this.setState({game})
  }

  answerSubmit() {
    const {game} = this.state
    const answer = game.answers[game.currentAlbumIndex] || {}
    const album = game.albums[game.currentAlbumIndex]
    const answerText = answer.text ? answer.text.trim().toUpperCase() : ''

    answer.isSubmitted = true
    answer.isCorrect = answerText && (album.name.includes(answerText) || album.artist.includes(answerText));
    game.answers[game.currentAlbumIndex] = answer
    this.setState({game})
  }

  answerGiveUp() {
    const {game} = this.state
    const answer = game.answers[game.currentAlbumIndex] || {}
    answer.isGivenUp = true
    game.answers[game.currentAlbumIndex] = answer
    this.setState({game})
  }

  playlistUriChange(playlistUri) {
    const {game} = this.state
    game.playlistUri = playlistUri
    this.setState({game})
    window.sessionStorage.setItem('playlistUri', playlistUri)
  }
}

export default GameContainer



