import React from 'react';
import './Common.css'
import './Welcome.css'

const Welcome = ({playlistUri, onStart, onPlaylistUriChange}) => {
  const submit = (event) => {
    event.preventDefault()
    onStart()
  }

  return (
    <div className="welcome">
      <div className="header">
        <h2>Album Quiz!</h2>
      </div>

      <div className="start">
        <form onSubmit={submit}>
          <label>
            <span>Paste a Spotify Playlist link here:</span>
            <input
              type="text"
              value={playlistUri}
              placeholder="or Rolling Stone 500"
              onChange={(e) => onPlaylistUriChange(e.target.value)}
              onClick={(e) => e.target.select()}
            />
          </label>

          <button>
            Start!
          </button>
        </form>
      </div>
    </div>
  );
}

export default Welcome;



