const parsePlaylistUri = (uri) => {
  if (!uri) return {}
  const matches = uri.match(/open.spotify.com\/user\/(.*)\/playlist\/(.*)/)
  if (!matches) throw new Error('Invalid Spotify Playlist URI')
  const [, user, playlist] = matches
  return {user, playlist}
}

export default parsePlaylistUri



