import React from 'react';
import {loadAndPixelateImageOnCanvas} from '../utils.js'
import './PixelatedImage.css'

export default function PixelatedImage({src, width, height, scale}) {
  const margins = 20;

  return <canvas
    className="pixelateImage"
    width={width}
    height={height}
    style={{width: window.screen.width - margins, maxWidth: width}}
    ref={(canvas) => canvas && loadAndPixelateImageOnCanvas(src, canvas, scale, width, height)}
  />
}



