export function queryStringToObj(queryString) {
  const map = {};
  const filteredQueryString = queryString[0] === '?' || queryString[0] === '#' ? queryString.substr(1) : queryString
  const ampersandSplit = filteredQueryString.split('&');

  for (let i = 0; i < ampersandSplit.length; i++) {
      const equalSplit = ampersandSplit[i].split('=')
      const key = decodeURIComponent(equalSplit[0])
      const value = decodeURIComponent(equalSplit[1] || '')
      map[key] = value
  }

  return map;
}

export function albumsReducer(store, item, index, playlistTrackItems) {
  if (
    store.albums.length === 25 ||
    store.albums.length === playlistTrackItems.length
  ) {
    return store;
  }

  const albumInfo = item.track.album
  const album = {
    id: albumInfo.id,
    name: albumInfo.name.toUpperCase(),
    artist: albumInfo.artists[0].name.toUpperCase(),
    image: albumInfo.images[0].url
  }

  if (!store.albumMap[album.id]) {
    store.albumMap[album.id] = true;
    store.albums.push(album)
  }

  return store;
}

export function answerStatsReducer(stats, answer) {
  if (answer.isSubmitted) stats.answered++
  if (answer.isSubmitted && answer.isCorrect) stats.correct++
  if (answer.isSubmitted && !answer.isCorrect) stats.incorrect++
  if (answer.isGivenUp) stats.givenUp++
  return stats
}

export function loadAndPixelateImageOnCanvas(src, canvas, scale, width, height) {
  const image = new Image()
  image.onload = () => pixelateImage(image, canvas, scale, width, height)
  image.src = src
}

const pixelateImage = (image, canvas, scale, width, height) => {
  scale = scale * 0.01

  const ctx = canvas.getContext('2d')
  const scaledW = width * scale
  const scaledH = height * scale

  ctx.mozImageSmoothingEnabled = false
  ctx.webkitImageSmoothingEnabled = false
  ctx.imageSmoothingEnabled = false

  ctx.drawImage(image, 0, 0, scaledW, scaledH)
  ctx.drawImage(canvas, 0, 0, scaledW, scaledH, 0, 0, width, height)
}



