import React from 'react';
import PixelatedImage from './PixelatedImage'
import './Common.css'
import './Game.css'

export default function Game({
  answers, albums, currentAlbumIndex, answered, correct, incorrect, givenUp,
  onAnswerSubmit, onAnswerChange, onSkip, onAnswerGiveUp, onQuit, onGoBack
}) {
  const answer = answers[currentAlbumIndex] || {text: ''}
  const album = albums[currentAlbumIndex]
  const revealAlbum = (answer.isSubmitted && answer.isCorrect) || answer.isGivenUp

  const answerSubmit = (e) => {
    e.preventDefault()
    onAnswerSubmit()
  }

  const answerChange = (e) => {
    e.preventDefault()
    onAnswerChange(e.target.value)
  }

  if (albums.length < 1) return (
    <div className="GameLoading">Loading, pls wait... (do you remember PS1?)</div>
  )

  return (
    <div className="game">
      <div className="nav">
        <div className="album-progress">
          {`Album #${currentAlbumIndex + 1} of ${albums.length}`}
        </div>

        <div className="answer-stats">
          <ul>
            <li className="stat answered">Answered: {`${answered} of ${albums.length}`}</li>
            <li className="stat correct">Correct: {`${correct}`}</li>
            <li className="stat incorrect">Incorrect: {`${incorrect}`}</li>
            <li className="stat givenup">Given Up: {`${givenUp}`}</li>
          </ul>
        </div>
      </div>

      <div className="answer">
        <form onSubmit={answerSubmit}>
          <input
            className={answer.isSubmitted ? (answer.isCorrect ? 'correct' : 'incorrect') : ''}
            type="text"
            placeholder={answer.isGivenUp ? 'You gave up on this one' : 'Album or Artist' }
            value={answer.text}
            onChange={answerChange}
            disabled={answer.isGivenUp}
          />

          {answer.isSubmitted &&
            <p className={`feedback ${answer.isCorrect ? 'correct' : 'incorrect'}`}>
              {answer.isCorrect ? `Correct! ${album.name} by ${album.artist}` : 'Incorrect'}
            </p>
          }

          {answer.isGivenUp &&
            <p className="feedback givenup">
              {`${album.name} by ${album.artist}`}
            </p>
          }
        </form>

        <div className="actions">
          {!answer.isCorrect && !answer.isGivenUp &&
            <button className="answer" onClick={answerSubmit}>
              Answer
            </button>
          }

          {!answer.isCorrect && !answer.isGivenUp &&
            <button className="giveup" onClick={onAnswerGiveUp}>
              Give up
            </button>
          }

          <button className="next" onClick={onSkip}>
            Next
          </button>

          <button className="prev" onClick={onGoBack}>
            Prev
          </button>

          <button className="quit" onClick={onQuit}>
            Quit
          </button>
        </div>
      </div>

      <PixelatedImage src={album.image} width={640} height={640} scale={revealAlbum ? 100 : 7} />
    </div>
  );
}




